import './App.css';
var React = require('react');
var ReactDOM = require('react-dom');


class Task extends React.Component {
		
		constructor(props) {
    		super(props);
    		this.state = {
      			edit: false,
    		};
  		};
		
  		edit = () => {
    		this.setState({edit: true});
  		};
		
  		remove = () => {
			this.props.deleteBlock (this.props.index);
  		};
		
		save = () => {
			var newTitle = this.refs.taskTitle.value;
			var newValue = this.refs.content.value;
			this.props.update (newTitle, newValue, this.props.index);
			this.setState({edit: false});
		};
		
  		rendNorm = () => {
    		return (
      		<div className="box">
        		<div className="text">{this.props.name}</div>
        		<p>{this.props.children}</p>
        		<div className="btnBox">
        			<button onClick={this.edit} className="btn light"></button>
        			<button onClick={this.remove} className="btn red"></button>
        		</div>
      		</div>
    		);
  		};
		
		 rendEdit = () => {
    		return (
      			<div className="box">
       				<input ref="taskTitle" type="text" defaultValue={this.props.name} />
        			<textarea ref="content" defaultValue={this.props.children}></textarea>
        			<button onClick={this.save} className="btn success"></button>
      			</div>
    		);
  		};

		render() {
    		if (this.state.edit) {
      			return this.rendEdit ();
    		} 	else {
      			return this.rendNorm ();
    		}
  		}
		
		
	}
	
class Field extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      	tasks: [
        'Task 1',
        'Task 2',
        'Task 3'
      ],
		about: [
        'content of "Task1"',
        'content of "Task2"',
        'content of "Task3"'
      ]
    };
  };
	
deleteBlock = (i) => {
    var arr = this.state.tasks;
	var aboutArr =this.state.about;
	aboutArr.splice(i, 1);
    arr.splice (i, 1);
	this.setState ({about: aboutArr});
    this.setState ({tasks: arr});
  };

 updateText = (text, newAbout, i) => {
    var tasksArr = this.state.tasks;
	var aboutArr = this.state.about;
    tasksArr[i] = text;
    aboutArr[i] = newAbout;
    this.setState ({tasks: tasksArr});
    this.setState ({about: aboutArr});
  };
	
addTask = (text) => {
	var arr = this.state.tasks;
    arr.push (text);
    this.setState ({tasks: arr});
}
eachOne = () => {
	var resArr =[];
	for (var i=0; i<this.state.tasks.length; i++){
		resArr.push(<Task key={i} index={i} name={this.state.tasks[i]} update={this.updateText} deleteBlock={this.deleteBlock}>{this.state.about[i]}</Task>);
	}
	return(
		resArr
	);
};

  render() 
	{
    	return (
      		<div className="field">
				<button onClick={this.addTask.bind (null, 'default task')} className="btn new"> </button>
				{this.eachOne()}
      		</div>
    	);
  	}
}
	var output = document.getElementById('root')
    ReactDOM.render(<Field />, output);


